body{
    background-image: linear-gradient( 65.4deg,  rgba(56,248,249,1) -9.1%, 
    rgba(213,141,240,1) 48%, rgba(249,56,152,1) 111.1% );
}
.main{
    height: 50px;
    background-image: linear-gradient( 65.4deg,  rgba(249,56,152,1) -9.1%, 
    rgba(213,141,240,1) 48%, rgba(56,248,249,1) 111.1% );
    display: flex;
}
h3{
    height: 50px;
    color: #ffffff;
    transform: translate(0px, 5px);
}
.btn-outline-danger {
    transform: translate(83rem, 5px);
    width: 80px;
    height: 40px;
}
.btn-primary{
    transform: translate(42rem, 20rem);
    width: 15rem;
    height: 5rem;
}